
html,
body {
  font-family: Verdana;
  font-size: 13px;
  height: 100%;
}
ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}
a {
  color: #000;
  cursor: pointer;
  text-decoration: none;
}
#app {
  width: 85%;
  min-height: 80px;
  margin: 0 auto;
  position: relative;
  background-color: #f6f6ef;
}
.view {
  position: absolute;
  background-color: #f6f6ef;
  width: 100%;
  transition: opacity 0.2s ease;
  box-sizing: border-box;
  padding: 8px 20px;
}
.view.v-enter,
.view.v-leave {
  opacity: 0;
}


