
.item {
  padding: 2px 0 2px 40px;
  position: relative;
  transition: background-color 0.2s ease;
}
.item p {
  margin: 2px 0;
}
.item .title:visited {
  color: #828282;
}
.item .index {
  color: #828282;
  position: absolute;
  width: 30px;
  text-align: right;
  left: 0;
  top: 4px;
}
.item .domain,
.item .subtext {
  font-size: 11px;
  color: #828282;
}
.item .domain a,
.item .subtext a {
  color: #828282;
}
.item .subtext a:hover {
  text-decoration: underline;
}


