
.news-header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  padding: 2px;
  background-color: #f60;
}
.news-header .logo {
  width: 18px;
  height: 18px;
  background-image: url("../assets/logo.gif");
  border: 1px solid #fff;
  margin-right: 4px;
}
.news-header .nav {
  -ms-flex-positive: 1;
      flex-grow: 1;
}
.news-header .nav .title {
  font-weight: bold;
  margin-right: 10px;
}
.news-header .nav .tab::after {
  content: '|';
  color: #000;
  margin-left: 3px;
}
.news-header .nav .tab:last-child::after {
  content: '';
}
.news-header .nav .tab.router-link-active {
  color: #fff;
}
.news-header .project-info {
  color: #fff;
}
.news-header .project-info .source {
  color: #fff;
}


