
.news-view {
  padding-left: 5px;
  padding-right: 15px;
}
.news-view.loading:before {
  content: "Loading...";
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
}
.news-view .pagination {
  padding: 10px 10px 10px 40px;
  margin-top: 10px;
  border-top: 2px solid #f60;
}
.news-view .pagination a {
  margin-right: 10px;
}
.news-view .pagination a:hover {
  text-decoration: underline;
}


